// 引入
@import "./_include/_include.sass";

body.blur {
  #wrapper {
    filter: blur(1px);
    position: fixed;
    top: 0;
    left: 0;
    @include size(100%);
    overflow: hidden; } }

.swiper-slide {
  img {
    display: block;
    width: 100%; } }

#left-site {
  .pic-container {
    position: relative;
    margin-bottom: 15px; }
  .gallery-top {
    padding-right: 140px;
    padding-bottom: 30px;
    .swiper-slide {
      img {
        object-fit: cover;
        object-position: 50% 50%;
        opacity: 0;
        @include t(1s); }
      &.swiper-slide-active {
        img {
          opacity: 1; } } }
    .swiper-button-next {
      right: 150px; }
    .swiper-pagination {
      width: calc(100% - 140px);
      padding: 5px 0;
      bottom: 0;
      font-size: 12px;
      line-height: 19px;
      background-color: $color_main;
      border-radius: 2px;
      @include cr(#fff); } }
  .gallery-thumbs {
    position: absolute;
    right: 0;
    top: 0;
    width: 130px;
    height: 100%;
    .swiper-slide {
      // min-height: 100px
      cursor: pointer;
      overflow: hidden;
      opacity: 0.6;
      @include t(.5s);
      &.swiper-slide-thumb-active {
        opacity: 1; }
      img {
        width: auto;
        height: 100%;
        object-fit: cover;
        object-position: 50% 50%; } } } }

#right-site {
  padding-top: 50px;
  position: relative;
  top: 0;
  left: 0; }
//info
.info {
  padding: 20px 25px 10px;
  margin-bottom: 15px;
  background-color: rgba($color_bg_1,.1);
  border-radius: 2px;
  ul {
    display: flex;
    flex-wrap: wrap;
    border-radius: 2px;
    li {
      display: flex;
      margin-bottom: 10px;
      width: calc(100% / 3);
      font-size: 14px;
      border-radius: 2px; }
    &.full li {
      width: 100%; } }
  span {
    display: block;
    padding-right: 10px;
    margin-right: 10px;
    min-width: 80px;
    text-align: justify;
    text-align-last: justify;
    border-right: solid 1px rgba($color_main,.3);
    @include cr(rgba($color_main,.5)); }
  a {
    text-decoration: none;
    @include cr($color_main); }
  p {
    display: flex;
    align-items: center;
    letter-spacing: 1px;
    font-weight: bold;
    &.yes:before,&.no:before,img {
      display: block;
      margin-right: 15px;
      @include size(20px); }
    &.yes:before,&.no:before {
      content: "";
      background-size: cover; }
    &.yes:before {
      background-image: url(../images/yes.svg); }
    &.no:before {
      background-image: url(../images/no.svg); } } }

.map {
  padding-bottom: 50%;
  margin-bottom: 10px;
  position: relative;
  iframe {
    position: absolute;
    bottom: 0;
    top: 0;
    width: 100%;
    height: 100%; } }

.detail {
  padding-bottom: 20px; }

.description {
  padding-bottom: 20px;
  position: relative;
  .fav {
    display: flex;
    align-items: center;
    position: absolute;
    right: 25px;
    top: 20px;
    cursor: pointer;
    p {
      font-size: 12px;
      font-weight: normal; }
    img {
      display: block;
      margin-left: 5px;
      @include size(30px,auto);
      filter: grayscale(1); }
    &.saved {
      img {
        animation: fav 1s both; } } }

  .price {
    margin-bottom: 20px; }
  h4 {
    display: flex;
    margin-bottom: 10px;
    align-items: flex-end;
    font-size: 28px;
    font-weight: bold;
    @include cr(#f25c54);
    i {
      display: block;
      margin-left: 10px;
      font-style: inherit;
      font-size: 16px;
      line-height: 20px; } }
  li {
    padding: 5px 10px;
    background-color: rgba($color_bg_1,.1); } }

.contact {
  .person {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    .head {
      border-radius: 50%;
      background-color: rgba($color_bg_1,.1);
      overflow: hidden;
      @include size(100px);
      img {
        display: block;
        margin: 0 auto;
        height: 100%;
        width: auto;
        object-fit: cover; } }
    p {
      margin-left: 10px;
      align-items: flex-end;
      width: calc(100% - 120px);
      i {
        font-style: inherit;
        font-size: 12px;
        line-height: 21px; } } }
  .phone {
    margin-bottom: 15px;
    background-color: #ffe1a8;
    border-radius: 100px;
    a {
      display: flex;
      align-items: center;
      padding: 15px 20px;
      padding-left: 50px;
      img {
        display: block;
        @include size(30px);
        animation: shake .5s infinite alternate; }
      p {
        padding-right: 20px;
        width: calc(100% - 30px);
        font-size: 24px;
        justify-content: center; } } }
  .functions {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-bottom: 10px;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 5px;
      padding: 10px;
      border-radius: 100px;
      width: calc(100% / 3 - 10px);
      @include cr(#fff);
      background-color: #0db39e;
      &.no-service {
        background-color: rgba($color_bg_1,.2);
        cursor: not-allowed;
 }        // pointer-events: none
      &.red {
        background-color: #f25c54; }
      &.full {
        width: calc(100% - 10px); }
      img {
        display: block;
        margin-right: 10px;
        @include size(16px); } } } }

.tips {
  padding-bottom: 20px;
  p {
    img {
      @include size(50px);
      animation: billing 1.5s infinite alternate linear; } } }

#message {
  padding-bottom: 20px;
  h3 {
    display: flex;
    justify-content: space-between;
    align-items: flex-end; }
  span {
    text-align-last: left;
    text-align: left;
    padding: 0;
    margin: 0;
    border: none;
    font-size: 12px; }
  li {
    display: block;
    position: relative;
    padding-bottom: 20px;
    border-bottom: solid 1px rgba($color_bg_1,.1);
    &:last-of-type {
      border-bottom: none;
      padding-bottom: 0; } }
  .date {
    position: absolute;
    right: 0;
    top: 4px;
    @include cr(rgba($color_bg_1,.5)); }
  .reply {
    h4 {
      display: flex;
      align-items: center;
      width: calc(100% - 135px);
      padding-right: 20px;
      margin-bottom: 10px;
      &:before {
        display: block;
        content: "";
        margin-right: 10px;
        @include size(30px);
        background-size: contain;
        background-image: url('../images/question.svg'); }
      em {
        @include cr(rgba($color_bg_1,.5));
        font-style: inherit;
        font-size: 12px; } }
    .anwser {
      background-color: #f8f9fa;
      width: calc(100% - 30px);
      margin-left: 30px;
      padding: 10px 20px;
      border-radius: 1000px;
      position: relative;
      &:before {
        content: "";
        display: block;
        position: absolute;
        bottom: 100%;
        left: 30px;
        @include size(0);
        border: solid 6px transparent;
        border-bottom-color: #f8f9fa; } } }
  textarea {
    background-color: #f8f9fa;
    border-color: rgba($color_bg_1,.2);
    border-radius: 2px;
    height: 200px;
    padding: 10px 10px 50px;
    resize: none;
    &:focus {
      outline: none; } }
  .txt-area {
    margin-bottom: 15px;
    position: relative;
    .nums {
      display: flex;
      align-items: flex-end;
      position: absolute;
      bottom: 10px;
      right: 15px;
      font-size: 16px;
      letter-spacing: 2px;
      i {
        font-size: 20px;
        font-style: inherit;
        @include cr(#2bbbad); } } } }


#notice {
  display: none;
  position: fixed;
  right: 50%;
  top: 5%;
  transform: translate(50%,0%);
  z-index: 5;
  background-color: #fff;
  padding: 10px 25px;
  border-radius: 100px;
  p {
    color: #fff; }
  &.successed {
    background-color: #0db39e; }
  &.failed {
    background-color: #f25c54; } }


