// 引入
@import "./_include/_include.sass";

* {
  vertical-align: middle;
  font-family: 'Microsoft JhengHei'; }

h1,h2,h3,h4,h5,h6,p {
  margin: 0; }


ul {
  margin: 0;
  padding: 0;
  li {
    list-style-type: none; } }

[data-bg] {
  display: block;
  @include size(100%,auto);
  background-size: cover;
  background-repeat: no-repeat; }

.flex-row {
  display: flex;
  flex-wrap: wrap; }

body {
  animation: fade 2s 1s both; }

h1 {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  letter-spacing: 2px;
  @include cr($color_main); }
h2 {
  display: block;
  padding-bottom: 15px;
  letter-spacing: 2px;
  margin-bottom: 20px;
  font-size: 20px;
  border-bottom: solid 2px rgba($color_bg_1,.2);
  @include cr($color_bg_1);
  font-weight: bold;
  position: relative;
  &:after {
    content: "";
    display: block;
    @include size(85px,2px);
    background-color: #52b788;
    position: absolute;
    bottom: -2px;
    left: 0; } }
h3 {
  font-size: 18px;
  padding: 20px 0;
  font-weight: bold;
  @include cr($color_bg_1); }
h4 {
  font-size: 16px; }
h5 {
  font-size: 14px; }
h6 {
  font-size: 12px;
  @include cr(#777); }

p {
  @include cr(#4a4e69); }

#wrapper {
  padding: 100px 0;
  border-top: solid 10px $color_main;
  border-bottom: solid 10px $color_main;
  background-color: #f8f8f8; }

.container {
  width: 1280px; }
