// 引入
@import "./_include/_include.sass";

.fade {
  opacity: 0;
  &.animated {
    @keyframes fade {
      0% {
        opacity: 0; }
      100% {
        opacity: 1; } }
    animation: fade 1s both; } }

.fadeDown {
  opacity: 0;
  &.animated {
    @keyframes fadeDown {
      0% {
        transform: translateY(-50px);
        opacity: 0; }
      100% {
        opacity: 1;
        transform: translateY(0); } }
    animation: fadeDown 1s both; } }

.fadeUp {
  opacity: 0;
  &.animated {
    @keyframes fadeUp {
      0% {
        transform: translateY(50px);
        opacity: 0; }
      100% {
        opacity: 1;
        transform: translateY(0); } }
    animation: fadeUp 1s both; } }

.fadeLeft {
  opacity: 0;
  &.animated {
    @keyframes fadeLeft {
      0% {
        transform: translateX(-50px);
        opacity: 0; }
      100% {
        opacity: 1;
        transform: translateX(0); } }
    animation: fadeLeft 1s both; } }

.fadeRight {
  opacity: 0;
  &.animated {
    @keyframes fadeRight {
      0% {
        transform: translateX(50px);
        opacity: 0; }
      100% {
        opacity: 1;
        transform: translateX(0); } }
    animation: fadeRight 1s both; } }

.fadeScale {
  opacity: 0;
  &.animated {
    @keyframes fadeScale {
      0% {
        transform: scale(0.9) translateY(10px);
        opacity: 0; }
      100% {
        opacity: 1;
        transform: scale(1) translateY(0px); } }
    animation: fadeScale 1s both; } }



@keyframes shake {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(20deg); } }

@keyframes billing {
  0% {
    opacity: 0.7;
    transform: scale(1); }
  33% {
    opacity: 1;
    transform: scale(1.05); }
  66% {
    opacity: 0.7;
    transform: scale(1); }
  100% {
    opacity: 1;
    transform: scale(1.05); } }

@keyframes fav {
  0% {
    transform: scale(1) rotate(0deg); }
  20%,80% {
    transform: scale(0) rotate(180deg); }
  100% {
    transform: scale(1) rotate(360deg);
    filter: grayscale(0); } }

