// 引入
@import "./_include/_include.sass";

#fav-container {
  .fav-head {
    background-color: rgba($color_bg_1,.1);
    padding: 30px 25px 20px;
    margin-bottom: 30px;
    border-radius: 2px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    .fav-search {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      width: 80%; }
    .input {
      display: flex;
      align-items: center;
      margin-right: 20px;
      margin-bottom: 10px;
      label {
        padding-left: 25px;
        min-width: 60px;
        font-size: 16px;
        color: $color_main;
        &:before {
          margin-top: 4px; } }
      select {
        display: block;
        &+label {
          padding-left: 5px; } } } }

  .fav-body {
    .body-tool {
      display: flex;
      align-items: center;
      margin-bottom: 30px;
      padding-left: 15px;
      padding-right: 20px;
      padding-bottom: 20px;
      border-bottom: solid 1px rgba(74,78,105,0.1);
      label {
        padding-left: 25px;
        color: $color_main; }
      .btn {
        @include size(39px);
        padding: 0;
        margin-left: 20px;
        background-image: url(../images/bin.svg);
        background-size: 20px auto;
        background-repeat: no-repeat;
        background-position: center center; } }
    .item {
      display: flex;
      margin-bottom: 30px;
      padding-bottom: 30px;
      border-bottom: solid 1px rgba(74,78,105,0.2);
      position: relative;
      &:last-of-type {
        margin-bottom: 0;
        border-bottom: none; }
      .btn {
        position: absolute;
        right: 20px;
        top: 20px;
        @include size(39px);
        padding: 0;
        background-image: url(../images/bin.svg);
        background-size: 20px auto;
        background-repeat: no-repeat;
        background-position: center center; }
      .price {
        display: flex;
        align-items: flex-end;
        position: absolute;
        bottom: 20px;
        left: 20px;
        h4 {
          display: flex;
          align-items: flex-end;
          font-size: 28px;
          font-weight: bold;
          margin-right: 10px;
          margin-bottom: -1px;
          @include cr(#f25c54);
          i {
            display: block;
            margin-left: 10px;
            font-style: inherit;
            font-size: 16px;
            line-height: 20px; } } }
      .date {
        display: flex;
        position: absolute;
        bottom: 20px;
        right: 20px;
        i,p {
          font-style: inherit;
          font-size: 14px;
          @include cr(rgba(74,78,105,0.5)); }
        p {
          margin-right: 5px; } }
      figure {
        margin: 0;
        width: 30%;
        overflow: hidden;
        img {
          display: block;
          @include size(100%,auto);
          @include t(2s); }
        &:hover {
          img {
            transform: scale(1.1); } } }
      figcaption.info {
        margin: 0;
        padding-bottom: 50px;
        width: 65%;
        position: relative;
        h2 {
          width: calc(100% - 50px); }
        .contact {
          width: 70%;
          float: right;
          padding-top: 10px;
          margin-right: -10px;
          .functions {
            justify-content: flex-end;
            padding-bottom: 0;
            a {
              width: calc(100% / 4 - 10px); } } } }
      .select {
        width: 5%;
        display: flex;
        align-items: center;
        justify-content: center; } } }

  .fav-foot {
    display: flex;
    justify-content: center;
    padding: 40px 0;
    .pagination li.active a {
      background-color: $color_main; } } }

.datepicker-date-display {
  display: none; }

.datepicker-modal {
  max-width: 340px;
  min-width: 340px; }

.modal-overlay {
  // visibility: hidden
  opacity: 0;
  position: absolute;
  top: 0; }

#datepicker-container {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  @include size(100%);
  z-index: 100;
  background-color: rgba(#000,.5);
  .content {
    padding: 20px;
    border-radius: 3px;
    margin-bottom: 30px;
    background-color: #efefef; }
  .input-field {
    margin-bottom: 20px; }
  .form {
    padding: 50px 40px 20px;
    background-color: #f8f8f8;
    position: absolute;
    bottom: 50%;
    right: calc(50% - 200px);
    transform: translateY(50%);
    width: 400px;
    min-height: 340px;
    border-radius: 5px;
    .close {
      padding: 0;
      position: absolute;
      right: 10px;
      top: 10px;
      @include size(30px);
      border: none;
      background-color: transparent;
      cursor: pointer;
      background-image: url('../images/close.svg');
      background-size: 15px auto;
      background-repeat: no-repeat;
      background-position: center; }
    .btn {
      margin-top: 20px;
      width: 100%; }
    p {
      @include cr($color_main);
      font-size: 14px;
      &.ps {
        @include cr(#f25c54); } } } }

[type="checkbox"].filled-in:checked+span:not(.lever):after,
[type="checkbox"].filled-in:checked+span:not(.lever):before,
[type="checkbox"].filled-in:not(:checked)+span:not(.lever):after {
  top: 2px; }
